* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.blok-box {
  justify-content: center;
  align-items: center;
  width: 100vmin;
  height: 100vmin;
  margin: 0 auto;
  display: flex;
}

.stopwatch {
  z-index: 1;
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 82vmin;
  height: 82vmin;
  position: relative;
}

.stopwatch__minute-hand {
  z-index: 0;
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s linear infinite move;
  position: absolute;
  top: 24%;
  left: 48.08%;
}

@keyframes move {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.stopwatch__second-hand {
  z-index: 0;
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite move;
  position: absolute;
  top: 1%;
  left: 49%;
}

.stopwatch__circle {
  z-index: 1;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 46.73%;
  left: 46.73%;
}

.stopwatch--speed-up__second-hand {
  animation: 10s linear infinite move;
}

.stopwatch--speed-up__minute-hand {
  animation: 360s linear infinite move;
}
